.exerciseCategoryContainer {
  display: flex;
  gap: 28px;
  margin-bottom: 40px;
}
.activeCat {
  position: relative;
}
.activeCat::after {
  content: '';
  background-color: var(--accent-color);
  width: 100%;
  height: 4px;
  position: absolute;
  top: 22px;
  left: 0;
  border-radius: 2px;
}

.arrCont {
  display: flex;
  flex-wrap: wrap;
  margin: auto;
  gap: 20px;
  max-width: 335px;
  margin: auto;
}
.arrElCont {
  position: relative;
  max-width: 335px;
  max-height: 206px;
  color: var(--invent-color);
  overflow: hidden;
}
.arrElCont img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.arrElCont div {
  position: absolute;
  text-align: center;
  font-weight: 400;
  top: 0;
  left: 0;
  padding: 45px 105px 45px 105px;
  width: 125px;
  height: 116px;
  font-style: normal;
  font-feature-settings: 'clig' off, 'liga' off;
  font-family: Roboto;
}
.arrElCont h2 {
  font-size: 20px;
  line-height: 24px; /* 120% */
}
.arrElCont p {
  color: var(--calendar-arrow-color);

  font-size: 12px;
  line-height: 18px;
}

.exerciseItemContainer {
  width: 335px;
  height: 141px;
  background-color: black;
}
.exerciseItemContainer::before {
  content: 'WORKOUT';
  color: white;
  display: inline-flex;
  padding: 5px 7.5px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  border-radius: 4px;
  background: rgba(239, 237, 232, 0.05);
}
.exerciseItemContainer h3{
  color: white;
}

.exerciseItemContainer div{
  display: flex;
  color: grey;
}
.exerciseItemContainer div span{
  color: white;
}